p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  padding-left: 0;
  margin: 0;
}

button {
  cursor: pointer;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

body {
  font-family: 'DM Sans', Tahoma, sans-serif;
}

html {
  overflow-y: scroll;
  scroll-behavior: smooth;
}

.link {
  text-decoration: none;
  color: currentColor;
}

.list {
  list-style: none;
}

.no-scroll {
  overflow: hidden;
}
