.about {
  background-color: $bgc-about-gallery-contacts-footer;
  position: relative;

  background-image: url('/src/images/about-milk-mob-1x.png');
  background-size: contain;
  background-position: 0 78%;
  background-repeat: no-repeat;
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('/src/images/about-milk-mob-2x.png');
  }

  @include tab {
    background-image: url('/src/images/about-milk-desk-1x.png');
    background-position: 0 85%;
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('/src/images/about-milk-desk-2x.png');
    }
  }

  @include desk {
    background-image: url('/src/images/about-milk-desk-1x.png');
    background-position: center 110%;
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url('/src/images/about-milk-desk-2x.png');
    }
  }
}

.top-text {
  text-align: center;
  margin-bottom: 15px;
}

.about-title {
  text-align: center;
  margin-bottom: 32px;

  @include tab {
    margin-bottom: 40px;
  }
  @include desk {
    margin-bottom: 80px;
  }
}

.about-box {
  @include tab {
    display: flex;
    justify-content: space-between;
  }

  @include desk {
    justify-content: flex-start;
    align-items: center;
  }
}

.box-cow {
  margin-bottom: 40px;
  @include tab {
    max-width: 336px;
    margin-bottom: 0;
  }

  @include desk {
    min-width: 501px;
    margin-right: 139px;
  }
}

.text-first {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  max-width: 220px;
  margin-bottom: 40px;

  color: $text-color-black;

  @include tab {
    max-width: 334px;
    font-size: 14px;
  }

  @include desk {
    min-width: 463px;
    font-size: 16px;
    line-height: 1.75;
  }
}

.text-second {
  font-family: 'DM Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  max-width: 220px;
  margin-bottom: 20px;

  color: $text-color-second;

  @include tab {
    max-width: 334px;
    font-size: 14px;
    margin-bottom: 40px;
  }

  @include desk {
    min-width: 463px;
    font-size: 16px;
    line-height: 1.75;
  }
}

