  border-radius: 22px;
  min-width: 176px;
  min-height: 44px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;

  align-items: center;
  justify-content: center;
  cursor: pointer;

  @include tran_sh_bg();

  &:hover,
  &:focus {
