.mob-menu {
  z-index: 21;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .4);
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  position: fixed;
  top: 0;
}

.mob-menu .container {
  margin: 0;
  padding: 0;
}

.mob-menu .section {
  padding: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.menu {
  width: 250px;
  height: 100%;
  background-color: #e17992;
  padding-top: 20px;
  padding-bottom: 171px;
  transition: transform .2s ease-in-out .2s;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(0%);
}

.mob-menu.is-hidden .menu {
  transform: translateX(100%);
}

.menu-close-btn {
  fill: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  margin-bottom: 26px;
  margin-left: 216px;
  padding: 0;
  transition: all .4s;
  display: block;
}

.menu-close-btn:hover .icon-cross, .menu-close-btn:focus .icon-cross {
  fill: #d41443;
}

.menu-order-btn {
  color: #d41443;
  background-color: #fafafa;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 22px;
  margin-bottom: 19px;
  margin-left: 20px;
  padding-top: 11px;
  padding-bottom: 12px;
  padding-left: 49px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  transition: box-shadow .2s ease-in-out, background-color .2s ease-in-out;
}

.menu-order-btn:hover, .menu-order-btn:focus {
  background-color: #ecebeb;
  box-shadow: 2px 2px 10px 2px #a05a6b;
}

.menu-order-btn .container {
  display: flex;
}

.menu-order-btn:after {
  content: ">";
  width: 5.26px;
  height: 8px;
  border-radius: 2px;
  margin-left: 28.47px;
  padding-right: 48.64px;
}

.mob-menu-list__item {
  border: 1px solid #e18298;
}

.mob-menu-list__link {
  color: #fff;
  align-items: center;
  padding: 13px 0 11px 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
