.geo-backdrop {
  z-index: 21;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .4);
  transition: opacity .2s ease-in-out, visibility .2s ease-in-out;
  position: fixed;
  top: 0;
}

.geomodal {
  width: 80vw;
  height: 80vh;
  transform: translate(-50%, -50%) scale(1) skew(0);
  background-color: #fff;
  border-radius: 5px;
  padding: 10px;
  transition: transform .2s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
}

.geo-backdrop.is-hidden .geomodal {
  transform: translate(0)scale(.5)skew(30deg);
}

.geomodal-close-btn {
  z-index: 21;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .643);
  border: 1px solid rgba(0, 0, 0, .3);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.geomodal-close-icon {
  fill: #000;
}

.scroll-up {
  z-index: 1000;
  height: 50px;
  width: 50px;
  color: #ffa5ba;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  border-radius: 50%;
  transition: all .2s;
  position: fixed;
  bottom: 30px;
  right: 30px;
  transform: translateY(25px);
  box-shadow: inset 0 0 0 4px #d41443;
}

.scroll-up:before {
  width: 100%;
  height: 100%;
  content: "⇧";
  justify-content: center;
  align-items: center;
  font-size: 35px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.scroll-up:hover {
  color: #d41443;
}

.scroll-up--activity {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.scroll-up___svg-path {
  box-sizing: border-box;
  fill: none;
  stroke: #ffa5ba;
  stroke-width: 4px;
  transition: all .2s;
}

